import { Editor, Element, Point, Transforms, Path, Range, Text } from "slate"

// Range.isRangeslate; ElementisElementText.isText

const insertNodes = (editor, nodeRawData) => {
  Editor.withoutNormalizing(editor, () => {
    // options
    const hanging = false
    const voids = false
    const mode = "lowest"
    let at, match, select

    let nodes
    if (Node.isNode(nodeRawData)) {
      nodes = [nodeRawData]
      console.log("nodes = [nodeRawData]")
    } else {
      nodes = nodeRawData
      console.log("nodes = nodeRawData")
    }

    if (nodes.length === 0) {
      console.log("nodes.length === 0")
      return
    }

    const [node] = nodes

    // By default, use the selection as the target location. But if there is
    // no selection, insert at the end of the document since that is such a
    // common use case when inserting from a non-selected state.
    if (!at) {
      if (editor.selection) {
        at = editor.selection
      } else if (editor.children.length > 0) {
        at = Editor.end(editor, [])
      } else {
        at = [0]
      }

      select = true
    }

    if (select == null) {
      select = false
    }

    if (Range.isRange(at)) {
      if (!hanging) {
        at = Editor.unhangRange(editor, at, { voids })
      }

      if (Range.isCollapsed(at)) {
        at = at.anchor
      } else {
        const [, end] = Range.edges(at)
        const pointRef = Editor.pointRef(editor, end)
        Transforms.delete(editor, { at })
        at = pointRef.unref()
      }
    }

    if (Point.isPoint(at)) {
      if (match == null) {
        if (Text.isText(node)) {
          match = (n) => Text.isText(n)
        } else if (editor.isInline(node)) {
          match = (n) => Text.isText(n) || Editor.isInline(editor, n)
        } else {
          match = (n) => Element.isElement(n) && Editor.isBlock(editor, n)
        }
      }

      const [entry] = Editor.nodes(editor, {
        at: at.path,
        match,
        mode,
        voids,
      })

      if (entry) {
        const [, matchPath] = entry
        const pathRef = Editor.pathRef(editor, matchPath)
        const isAtEnd = Editor.isEnd(editor, at, matchPath)
        Transforms.splitNodes(editor, { at, match, mode, voids })
        const path = pathRef.unref()
        at = isAtEnd ? Path.next(path) : path
      } else {
        return
      }
    }

    const parentPath = Path.parent(at)
    let index = at[at.length - 1]

    if (!voids && Editor.void(editor, { at: parentPath })) {
      return
    }

    for (const node of nodes) {
      const path = parentPath.concat(index)
      index++
      editor.apply({ type: "insert_node", path, node })
      at = Path.next(at)
    }
    at = Path.previous(at)

    if (select) {
      const point = Editor.end(editor, at)

      if (point) {
        Transforms.select(editor, point)
      }
    }
  })
}

const wrapNodes = (editor, nodeRawData) => {
  // nodeRawDataelement
  // Editor.withoutNormalizing
  withoutNormalizing(editor, () => {
    const mode = "lowest"
    const split = true // truefalse
    const voids = false
    let match = null
    let at = editor.selection

    if (!at) {
      return
    }

    //  match == null
    if (Path.isPath(at)) {
      console.log("Path.isPath(at) === true")
      match = matchPath(editor, at)
    } else if (editor.isInline(nodeRawData)) {
      console.log(
        "Path.isPath(at) === false && editor.isInline(nodeRawData) === true" // codeSpan
      )
      match = (n) =>
        (Element.isElement(n) && Editor.isInline(editor, n)) || Text.isText(n)
    } else {
      console.log(
        "Path.isPath(at) === false && editor.isInline(nodeRawData) === false"
      )
      match = (n) => Element.isElement(n) && Editor.isBlock(editor, n)
    }

    if (split && Range.isRange(at)) {
      // at
      console.log("split === true && Range.isRange(at) === true")
      const [start, end] = Range.edges(at) // abcd[ef]start4end6
      const rangeRef = Editor.rangeRef(editor, at, {
        affinity: "inward",
      })
      console.log(rangeRef)
      Transforms.splitNodes(editor, { at: end, match, voids })
      Transforms.splitNodes(editor, { at: start, match, voids })
      at = rangeRef.unref()

      //  if(options.at == null) 
      Transforms.select(editor, at)
    }

    const roots = Array.from(
      Editor.nodes(editor, {
        at,
        match: editor.isInline(nodeRawData)
          ? (n) => Element.isElement(n) && Editor.isBlock(editor, n)
          : (n) => Editor.isEditor(n),
        mode: "lowest",
        voids,
      })
    )
    console.log("")
    console.log(editor.children)

    for (const [, rootPath] of roots) {
      const a = Range.isRange(at)
        ? Range.intersection(at, Editor.range(editor, rootPath))
        : at

      if (!a) {
        continue
      }

      const matches = Array.from(
        Editor.nodes(editor, { at: a, match, mode, voids })
      )

      if (matches.length > 0) {
        const [first] = matches
        const last = matches[matches.length - 1]
        const [, firstPath] = first
        const [, lastPath] = last

        if (firstPath.length === 0 && lastPath.length === 0) {
          // if there's no matching parent - usually means the node is an editor - don't do anything
          continue
        }

        const commonPath = Path.equals(firstPath, lastPath)
          ? Path.parent(firstPath)
          : Path.common(firstPath, lastPath)

        const range = Editor.range(editor, firstPath, lastPath)
        const commonNodeEntry = Editor.node(editor, commonPath)
        const [commonNode] = commonNodeEntry
        const depth = commonPath.length + 1
        const wrapperPath = Path.next(lastPath.slice(0, depth))
        const wrapper = { ...nodeRawData, children: [] }
        Transforms.insertNodes(editor, wrapper, { at: wrapperPath, voids })

        Transforms.moveNodes(editor, {
          at: range,
          match: (n) =>
            Element.isAncestor(commonNode) && commonNode.children.includes(n),
          to: wrapperPath.concat(0),
          voids,
        })
      }
    }

    console.log("codeSpan")
    console.log(editor.children)
  })

  console.log("")
  console.log(editor.children)
}

const matchPath = (editor, path) => {
  const [node] = Editor.node(editor, path)
  return (n) => n === node
}

const withoutNormalizing = (editor, fn) => {
  const value = Editor.isNormalizing(editor)
  Editor.setNormalizing(editor, false)
  try {
    fn()
  } finally {
    Editor.setNormalizing(editor, value)
  }
  // Editor.normalize(editor)
  normalize(editor)
  console.log("Normalize")
  console.log(editor.children)
}

const DIRTY_PATH_KEYS = new WeakMap()
const DIRTY_PATHS = new WeakMap()

const normalize = (editor, options) => {
  // const { force = false, operation } = options 
  const force = false
  let operation

  const getDirtyPaths = (editor) => {
    return DIRTY_PATHS.get(editor) || []
  }

  const getDirtyPathKeys = (editor) => {
    return DIRTY_PATH_KEYS.get(editor) || new Set()
  }

  const popDirtyPath = (editor) => {
    const path = getDirtyPaths(editor).pop()
    const key = path.join(",")
    getDirtyPathKeys(editor).delete(key)
    return path
  }

  if (!Editor.isNormalizing(editor)) {
    return
  }

  if (force) {
    const allPaths = Array.from(Node.nodes(editor), ([, p]) => p)
    const allPathKeys = new Set(allPaths.map((p) => p.join(",")))
    DIRTY_PATHS.set(editor, allPaths)
    DIRTY_PATH_KEYS.set(editor, allPathKeys)
  }

  if (getDirtyPaths(editor).length === 0) {
    console.log("return ")
    console.log(editor.children)
    return
  }

  console.log("")

  Editor.withoutNormalizing(editor, () => {
    /*
      Fix dirty elements with no children.
      editor.normalizeNode() does fix this, but some normalization fixes also require it to work.
      Running an initial pass avoids the catch-22 race condition.
    */
    for (const dirtyPath of getDirtyPaths(editor)) {
      if (Node.has(editor, dirtyPath)) {
        const entry = Editor.node(editor, dirtyPath)
        const [node, _] = entry

        /*
          The default normalizer inserts an empty text node in this scenario, but it can be customised.
          So there is some risk here.

          As long as the normalizer only inserts child nodes for this case it is safe to do in any order;
          by definition adding children to an empty node can't cause other paths to change.
        */
        if (Element.isElement(node) && node.children.length === 0) {
          editor.normalizeNode(entry, { operation })
        }
      }
    }

    let dirtyPaths = getDirtyPaths(editor)
    const initialDirtyPathsLength = dirtyPaths.length
    let iteration = 0

    while (dirtyPaths.length !== 0) {
      if (
        !editor.shouldNormalize({
          dirtyPaths,
          iteration,
          initialDirtyPathsLength,
          operation,
        })
      ) {
        return
      }

      const dirtyPath = popDirtyPath(editor)

      // If the node doesn't exist in the tree, it does not need to be normalized.
      if (Node.has(editor, dirtyPath)) {
        const entry = Editor.node(editor, dirtyPath)
        editor.normalizeNode(entry, { operation })
      }
      iteration++
      dirtyPaths = getDirtyPaths(editor)
    }
  })
}

const SculptTest = {
  insertNodes,
  wrapNodes,
  withoutNormalizing,
}

export default SculptTest
