import s from "./Sculpt.module.scss"
import { createEditor, Range, Transforms } from "slate"
import { Editable, Slate, withReact } from "slate-react"
import { withHistory } from "slate-history"
import React, { useEffect, useMemo, useRef, useState } from "react"
import { EDITABLE_STYLE } from "./SculptStyles"
import EditorUtils from "./EditorUtils"
import Prompt from "../../components/Prompt/desktop/Prompt"
import PromptContext from "../../components/Prompt/desktop/PromptContext"
import ActionPanel from "./ActionPanel"
import ActionPanelContext from "./ActionPanelContext"
import {
  getBasicStamp,
  getRealStamp,
  getSmallStamp,
} from "../../utils/timeStamp"

function Sculpt(props) {
  // last saved

  // Recover from Localstorage

  // ""

  // Database not connected // localhost! spam only support localhost
  // not connectedconnectedunconnect
  // intertext
  //  Connect DatabaseDo not connect database
  //  Save Choose Import Exit //  id save
  // importstart new Export Import Exit
  // meta
  //  exportbackup

  // Import Export // New Save
  // () backup
  // save  
  //  
  // post
  // 
  // getFiles:  timestamp id // import getFiles 
  // getFile: id 
  // saveFile: id+
  // newFile:  id
  // id 

  // importimportdatabase

  // edit linkOK
  // codeLinestab

  // ()

  // meta  Introduction to Xxx urlintroduction-to-xxx 
  // BUG: codeBlocktextBlock!
  // sizecodeBlock
  // 
  // { type: "codeSpan", size: "", children: [...]}
  // { type: "link", url: "...", text: "..." } // textchildren! 

  // codeLinecodeBlock
  // codeLine focus
  // 
  // 
  //   
  //  (+path/github) px

  // 

  // sculptmeta
  //  pattern  title  (Sculpt) meta tab
  //  ... 
  //   text, url, context 

  const [isUsingDatabase, setIsUsingDatabase] = useState(
    props.databaseUrl !== ""
  )
  const fileInputRef = useRef()
  const [isSlateRefreshing, setIsSlateRefreshing] = useState(false)
  // const [fileId, setFileId] = useState("") //  fileIdmeta.id
  // const [textDataToken, setTextDataToken] = useState(Math.random()) // textData
  const [isShowPrompt, setIsShowPrompt] = useState(false)
  const [promptData, setPromptData] = useState({})
  const [isShowActionPanel, setIsShowActionPanel] = useState(false)
  const [actionPanelData, setActionPanelData] = useState({})
  const [metaData, setMetaData] = useState({
    title: "",
    toBottom: true,
    baseURL: "", //  // baseURL
    // 
    color: [], // 
    fileNameStyle: "YYYYMMDD-HHMMSS",
    textTabWidth: 32, // px
    codeTabWidth: 2, // codetab
    // 
    timeStamp: Date.now(),
    id: "", //  import export
  })
  const setMetaId = (id) => {
    setMetaData({ ...metaData, id })
  }
  const getMetaId = () => {
    return metaData.id
  }

  const [lastExportedTimeStamp, setLastExportedTimeStamp] = useState(0)

  const [monospaceLetterWidth, setMonospaceLetterWidth] = useState(0)
  const hiddenMonospaceLetterRef = useRef()

  // 
  const [infoPanelContent, setInfoPanelContent] = useState([])
  const addInfoPanelContent = (text) => {
    setInfoPanelContent([...infoPanelContent, { text, timeStamp: Date.now() }])
    setTimeout(() => {
      infoPanelContent.shift()
      setInfoPanelContent([...infoPanelContent])
    }, 4000)
  }

  useEffect(() => {
    setMonospaceLetterWidth(
      hiddenMonospaceLetterRef.current.getBoundingClientRect().width
    )
  }, [])

  const withInlines = (editor) => {
    // editoreditor
    // inlinetype
    const { isInline } = editor
    editor.isInline = (element) =>
      ["codeSpan", "linkSpan"].includes(element.type) || isInline(element)
    return editor
  }
  const editor = useMemo(
    () => withInlines(withHistory(withReact(createEditor()))),
    []
  ) // withHistoryctrl+z

  // inlinewithInlines
  const [textData, setTextData] = useState([
    // value
    {
      type: "textBlock",
      children: [{ text: "", size: "normal" }],
    },
  ])
  // 

  const InlineChromiumBugfix = () => (
    // inline-block
    <span contentEditable={false} className={s.inlineChromiumBugfix}>
      ${String.fromCodePoint(160) /* Non-breaking space */}
    </span>
  )
  const Endo = (props) => {
    const { color, oblique, strong, size, line } = props.leaf
    const strongClassName = strong ? s.strong + " " : ""
    const obliqueClassName = oblique ? s.oblique + " " : ""
    const colorClassName = color ? s.color + " " : ""
    const lineClassName = s[line + "Line"] + " "
    const sizeClassName = s[size + "Size"] + " "
    return (
      <span
        {...props.attributes}
        className={
          strongClassName +
          obliqueClassName +
          colorClassName +
          lineClassName +
          sizeClassName
        }
      >
        {props.children}
      </span>
    )
  }
  // codeSpan
  //  01 02 03 04 

  const Nodo = (props) => {
    const { attributes, children, element } = props
    const type = element.type
    if (type === "textBlock") {
      return (
        <p {...attributes} className={s.textBlock}>
          {children}
        </p>
      )
    } else if (type === "codeSpan") {
      return (
        <span {...attributes} className={s.codeSpan}>
          <InlineChromiumBugfix />
          {children}
          <InlineChromiumBugfix />
        </span>
      )
    } else if (type === "linkSpan") {
      return (
        <a
          {...attributes}
          className={s.linkSpan}
          target={"_blank"}
          href={element.url}
          rel={"noreferrer"}
          onClick={(ev) => {
            ev.preventDefault()
            operateLink()
          }}
        >
          <InlineChromiumBugfix />
          {children}
          <InlineChromiumBugfix />
        </a>
      )
    } else if (type === "codeBlock") {
      return (
        <div {...attributes} className={s.codeBlock}>
          {children}
        </div>
      )
    } else if (type === "codeLine") {
      // /codeLine
      // path
      // 

      // codeLine normalsize
      // codeBlock
      const text = element.children[0].text
      const matchResult = text.match(/^\s+/)
      const spaceAmount = matchResult ? matchResult[0].length : 0

      const style = {
        textIndent: -spaceAmount * monospaceLetterWidth,
        paddingLeft: spaceAmount * monospaceLetterWidth,
      }
      return (
        <div {...attributes} className={s.codeLine} style={style}>
          {children}
        </div>
      )
    } else {
      return <p {...attributes}>{children}</p>
    }
  }

  // EndoLeaf NodoElement

  // type: "normal" | "code" - n q (normal code )
  // size: "small" | "normal" | "big" | "ultra" - 2 4 6 8
  // strong: true | false - b
  // oblique: true | false - i
  // color: "colored" | "plain" - g

  // BUG: codeBlock
  // slate -> 

  const operateLink = () => {
    const result = EditorUtils.getLinkActionType(editor)
    if (result[0] === "insert") {
      actInsertLink(result[1])
    } else if (result[0] === "edit") {
      actEditLink(result[1], result[2])
    }
  }
  const actInsertLink = (text) => {
    setActionPanelData({
      type: "insertLink",
      url: "",
      urlMust: "", // url 
      text,
      textMust: "",
      editor,
    })
    setIsShowActionPanel(true)
  }
  const actEditLink = (text, url) => {
    setActionPanelData({
      type: "editLink",
      url,
      urlMust: "", // url 
      text,
      textMust: "",
      editor,
    })
    setIsShowActionPanel(true)
  }
  const actSetMetaData = () => {
    setActionPanelData({
      type: "setMetaData",
      title: metaData.title,
      isTitleFocused: false,
      id: metaData.id,
      toBottom: metaData.toBottom,
      baseURL: metaData.baseURL,
      now: Date.now(),
      editor,
      onOK: (actionPanelData) => {
        const { title, toBottom, baseURL } = actionPanelData
        setMetaData({ ...metaData, title, toBottom, baseURL })
      },
    })
    setIsShowActionPanel(true)
  }
  const actChooseFile = async () => {
    const filesData = await getFiles()
    if (filesData instanceof Array) {
      if (filesData.length !== 0) {
        // 
        setActionPanelData({
          type: "chooseFile",
          filesData,
          onOK: async (id) => {
            // setFileId(id)
            const fileData = await getFile(id)
            changeFileData(fileData)
            setIsShowActionPanel(false)
          },
          chosenFileId: "",
          editor,
        })
        setIsShowActionPanel(true)
        // return
      } else {
        addInfoPanelContent("No file in the database. Test more words here.")
      }
    }
    // 
  }
  const saveFile = async (id, fileData, now) => {
    try {
      return await fetch(props.databaseUrl, {
        method: "post",
        body: JSON.stringify({
          type: "saveFile",
          id,
          data: fileData,
        }),
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((res) => res.json())
        .then(() => {
          setMetaData({ ...metaData, timeStamp: now })
          addInfoPanelContent("Saved at " + getSmallStamp(now) + ".")
        })
    } catch (err) {
      console.log(err)
      return undefined
    }
  }
  const newFile = async (fileData, now) => {
    try {
      return await fetch(props.databaseUrl, {
        method: "post",
        body: JSON.stringify({
          type: "newFile",
          data: fileData,
        }),
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((res) => res.json())
        .then((data) => {
          addInfoPanelContent("Created at " + getSmallStamp(now) + ".")
          return data.id
        })
    } catch (err) {
      console.log(err)
      return undefined
    }
  }
  const getFile = async (id) => {
    try {
      return await fetch(props.databaseUrl, {
        method: "post",
        body: JSON.stringify({
          type: "getFile",
          id,
        }),
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((res) => res.json())
        .then((json) => json.data)
    } catch (err) {
      console.log(err)
      return undefined
    }
  }
  const getFiles = async () => {
    try {
      return await fetch(props.databaseUrl, {
        method: "post",
        body: JSON.stringify({
          type: "getFiles",
        }),
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((res) => res.json())
        .then((json) => json.data)
    } catch (err) {
      console.log(err)
      return undefined
    }
  }
  const importFile = async (hasJsonData, jsonData) => {
    // importFileimport choose
    let json = {}
    if (hasJsonData) {
      json = jsonData
    } else {
      if (fileInputRef) {
        json = await fetch(
          URL.createObjectURL(fileInputRef.current.files[0])
        ).then((res) => res.json())
      }
    }
    json.meta.id = ""
    // setFileId("")
    changeFileData(json)
  }
  const changeFileData = (fileData) => {
    setIsSlateRefreshing(true)
    setTextData(fileData.content)
    setMetaData(fileData.meta)
    setTimeout(() => {
      setIsSlateRefreshing(false)
    })

    // setTimeout(() => {
    //   if (json.meta.toBottom) {
    //     goToBottom(true)
    //   } else {
    //     goToTop(true)
    //   }
    //   setIsInitialized(true)
    // }, 100)
  }
  const execSave = () => {
    const now = Date.now()
    const fileData = {
      meta: { ...metaData, timeStamp: now },
      content: editor.children,
    }
    if (getMetaId() === "") {
      newFile(fileData, now).then((id) => {
        setMetaId(id)
      })
    } else {
      saveFile(getMetaId(), fileData, now)
    }
  }
  const execChoose = () => {
    actChooseFile()
  }
  const execImport = () => {
    fileInputRef.current.click() // 
    // clickimportFile
  }
  const execExport = () => {
    const now = Date.now()
    const basicStamp = getBasicStamp(now)
    const sculptData = {
      meta: { ...metaData, timeStamp: now, id: "" },
      content: editor.children,
    }
    const file = new Blob([JSON.stringify(sculptData)], {
      type: "text/plain",
    })
    const url = URL.createObjectURL(file)
    const link = document.createElement("a")
    link.href = url
    if (metaData.title.length !== 0) {
      link.download =
        basicStamp +
        " " +
        metaData.title.replace(/[\\/:*?"<>|]/g, "#") +
        ".json" // 
    } else {
      link.download = basicStamp + ".json"
    }

    link.click()
    window.URL.revokeObjectURL(url)
    setLastExportedTimeStamp(Date.now())
  }

  const infoPaneNodes = () =>
    infoPanelContent.map((e) => (
      <div className={s.infoNode} key={e.timeStamp}>
        {e.text}
      </div>
    ))
  return (
    <>
      <div className={s.hiddenMonospaceLetter} ref={hiddenMonospaceLetterRef}>
        N
      </div>
      <div className={s.root}>
        <PromptContext.Provider
          value={{ isShowPrompt, setIsShowPrompt, promptData, setPromptData }}
        >
          <Prompt>
            <ActionPanelContext.Provider
              value={{
                isShowActionPanel,
                setIsShowActionPanel,
                actionPanelData,
                setActionPanelData,
              }}
            >
              <ActionPanel>
                <div className={s.frame}>
                  {!isSlateRefreshing && (
                    <Slate
                      editor={editor}
                      value={textData}
                      onChange={(value) => {
                        setTextData(value)
                      }}
                    >
                      <Editable
                        style={EDITABLE_STYLE} // 
                        autoFocus
                        spellCheck={false}
                        renderElement={(props) => <Nodo {...props} />}
                        renderLeaf={(props) => <Endo {...props} />}
                        onKeyDown={(ev) => {
                          if (ev.ctrlKey) {
                            if (ev.key === "b") {
                              EditorUtils.toggleStrong(ev, editor)
                            } else if (ev.key === "i") {
                              EditorUtils.toggleOblique(ev, editor)
                            } else if (ev.key === "g") {
                              EditorUtils.toggleColor(ev, editor)
                            } else if (ev.key === "u") {
                              EditorUtils.changeLine(ev, editor, "under")
                            } else if (ev.key === "d") {
                              EditorUtils.changeLine(ev, editor, "through")
                            } else if (ev.key === "q") {
                              EditorUtils.toggleCodeSpan(ev, editor)
                            } else if (ev.key === "4") {
                              EditorUtils.changeSize(ev, editor, "small")
                            } else if (ev.key === "3") {
                              EditorUtils.changeSize(ev, editor, "normal")
                            } else if (ev.key === "2") {
                              EditorUtils.changeSize(ev, editor, "big")
                            } else if (ev.key === "1") {
                              EditorUtils.changeSize(ev, editor, "ultra")
                            } else if (ev.key === "l") {
                              // ctrl+
                              //  ""
                              ev.preventDefault()
                              operateLink()
                            } else if (ev.key === "0") {
                              ev.preventDefault()
                              console.log(textData)
                            } else if (ev.key === "p") {
                              //  
                              EditorUtils.toggleCodeBlock(ev, editor)
                            } else if (ev.key === "v") {
                              EditorUtils.paste(ev, editor)
                            } else if (ev.key === "r") {
                              // ttab
                              EditorUtils.test(ev, editor)
                            }
                          } else if (ev.shiftKey) {
                            if (ev.key === "Tab") {
                              EditorUtils.shiftTab(ev, editor)
                            }
                          } else {
                            const { selection } = editor
                            if (ev.key === "Tab") {
                              EditorUtils.tab(ev, editor)
                            } else if (ev.key === "Enter") {
                              EditorUtils.enter(ev, editor)
                            } else if (
                              selection &&
                              Range.isCollapsed(selection)
                            ) {
                              // is-hotkeyhttps://github.com/ianstormtaylor/is-hotkey
                              if (ev.key === "ArrowLeft") {
                                ev.preventDefault()
                                Transforms.move(editor, {
                                  unit: "offset",
                                  reverse: true,
                                })
                              } else if (ev.key === "ArrowRight") {
                                ev.preventDefault()
                                Transforms.move(editor, { unit: "offset" })
                              }
                            }
                          }
                        }}
                      />
                    </Slate>
                  )}

                  <input
                    type="file"
                    className={s.fileInput}
                    ref={fileInputRef}
                    onChange={() => importFile(false)}
                  />
                  <div className={s.rightPanel}>
                    <div className={s.upperPanel}>
                      <div
                        className={
                          s.title +
                          " " +
                          (metaData.title === "" ? s.noTitle : "")
                        }
                        onClick={actSetMetaData}
                      >
                        {metaData.title === "" ? "No title" : metaData.title}
                      </div>
                      <div className={s.timeStamps}>
                        <div className={s.latestTimeStamp}>
                          {"[" + getRealStamp(metaData.timeStamp) + "]"}
                        </div>
                        {lastExportedTimeStamp !== 0 && (
                          <div className={s.lastExportedTimeStamp}>
                            {"Exported: [" +
                              getRealStamp(lastExportedTimeStamp) +
                              "]"}
                          </div>
                        )}
                      </div>

                      {isUsingDatabase ? (
                        <>
                          <div className={s.buttons}>
                            <div
                              className={s.save + " " + s.first}
                              onClick={execSave}
                            >
                              Save
                            </div>
                            <div className={s.choose} onClick={execChoose}>
                              Choose
                            </div>
                          </div>
                          <div className={s.buttons}>
                            <div className={s.export} onClick={execExport}>
                              Export
                            </div>
                            <div className={s.import} onClick={execImport}>
                              Import
                            </div>
                            <div
                              className={s.exit}
                              onClick={() => props.setIsInitialized(false)}
                            >
                              Exit
                            </div>
                          </div>
                        </>
                      ) : (
                        <div className={s.buttons}>
                          <div
                            className={s.export + " " + s.first}
                            onClick={execExport}
                          >
                            Export
                          </div>
                          <div className={s.import} onClick={execImport}>
                            Import
                          </div>
                          <div
                            className={s.exit}
                            onClick={() => props.setIsInitialized(false)}
                          >
                            Exit
                          </div>
                        </div>
                      )}
                    </div>
                    <div className={s.lowerPanel}>
                      <div className={s.buttonGroup}>
                        <div
                          className={s.strongButton}
                          onMouseDown={(ev) =>
                            EditorUtils.toggleStrong(ev, editor)
                          }
                        >
                          B
                        </div>
                        <div
                          className={s.obliqueButton}
                          onMouseDown={(ev) =>
                            EditorUtils.toggleOblique(ev, editor)
                          }
                        >
                          I
                        </div>
                        <div
                          className={s.underLineButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeLine(ev, editor, "under")
                          }
                        >
                          U
                        </div>
                        <div
                          className={s.deleteLineButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeLine(ev, editor, "through")
                          }
                        >
                          Del
                        </div>
                        <div
                          className={s.colorButton}
                          onMouseDown={(ev) =>
                            EditorUtils.toggleColor(ev, editor)
                          }
                        />
                      </div>
                      <div className={s.buttonGroup}>
                        <div
                          className={s.ultraSizeButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeSize(ev, editor, "ultra")
                          }
                        >
                          Ultra
                        </div>
                        <div
                          className={s.bigSizeButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeSize(ev, editor, "big")
                          }
                        >
                          Big
                        </div>
                        <div
                          className={s.normalSizeButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeSize(ev, editor, "normal")
                          }
                        >
                          Normal
                        </div>
                        <div
                          className={s.smallSizeButton}
                          onMouseDown={(ev) =>
                            EditorUtils.changeSize(ev, editor, "small")
                          }
                        >
                          Small
                        </div>
                      </div>
                      <div className={s.buttonGroup}>
                        <div
                          className={s.codeSpanButton}
                          onMouseDown={(ev) =>
                            EditorUtils.toggleCodeSpan(ev, editor)
                          }
                        >
                          code
                        </div>
                        <div
                          className={s.codeBlockButton}
                          onMouseDown={(ev) =>
                            EditorUtils.toggleCodeBlock(ev, editor)
                          }
                        >
                          code-block
                        </div>
                        <div
                          className={s.linkButton}
                          onClick={(ev) => {
                            operateLink()
                          }}
                        >
                          link
                        </div>
                      </div>
                    </div>
                    <div className={s.infoPanel}>{infoPaneNodes()}</div>
                  </div>
                </div>
              </ActionPanel>
            </ActionPanelContext.Provider>
          </Prompt>
        </PromptContext.Provider>
      </div>
    </>
  )
}
export default Sculpt
