import s from "./Symbols.module.scss"
import { useEffect } from "react"
function Symbols() {
  useEffect(() => {
    document.title = "clipboard"
  }, [])
  const greek = [
    ["alpha", "", ""],
    ["beta", "", ""],
    ["gamma", "", ""],
    ["delta", "", ""],
    ["epsilon", "", ""],
    ["zeta", "", ""],
    ["eta", "", ""],
    ["theta", "", ""],
    ["iota", "", ""],
    ["kappa", "", ""],
    ["lambda", "", ""],
    ["mu", "", ""],
    ["nu", "", ""],
    ["xi", "", ""],
    ["omicron", "", ""],
    ["pi", "", ""],
    ["rho", "", ""],
    ["sigma", "", ["", ""]],
    ["tau", "", ""],
    ["upsilon", "", ""],
    ["phi", "", ""],
    ["chi", "", ""],
    ["psi", "", ""],
    ["omega", "", ""],
  ]

  const math = [
    ["", ""],
    ["", ""],
    ["", ""],
    ["", ""],
    ["", ""],
  ]

  const copySelf = (str) => {
    navigator.clipboard.writeText(str)
  }

  return (
    <>
      <div className={s.center}>
        This page is merely designed for my personal use, and the layout style
        is yet bad
      </div>
      <div className={s.center}>HOW TO USE:</div>
      <div className={s.center}>
        Click and the letter/symbol will be copied to your clipboard, then you
        can paste it elsewhere
      </div>
      <div className={s.empty} />

      {[greek, math].map((group, groupIndex) => (
        <div className={s.grid} key={groupIndex}>
          {[...group].map((e, index) => (
            <div className={s.item} key={index}>
              <div className={s.name}>{e[0]}</div>
              <div className={s.content}>
                {e.length > 2 ? (
                  <>
                    <div className={s.left}>
                      <div className={s.letter} onClick={() => copySelf(e[1])}>
                        {e[1]}
                      </div>
                    </div>
                    {typeof e[2] === "string" ? (
                      <div className={s.right}>
                        <div
                          className={s.letter}
                          onClick={() => copySelf(e[2])}
                        >
                          {e[2]}
                        </div>
                      </div>
                    ) : (
                      <div className={s.right + " " + s.multiple}>
                        <div
                          className={s.letter}
                          onClick={() => copySelf(e[2][0])}
                        >
                          {e[2][0]}
                        </div>
                        <div
                          className={s.letter}
                          onClick={() => copySelf(e[2][1])}
                        >
                          {e[2][1]}
                        </div>
                      </div>
                    )}
                  </>
                ) : (
                  <div className={s.whole}>
                    <div className={s.letter} onClick={() => copySelf(e[1])}>
                      {e[1]}
                    </div>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      ))}
    </>
  )
}
export default Symbols
