import { Editor, Element, Range, Text, Transforms } from "slate"
import EditorBasics from "./EditorBasics"
import SculptTest from "./SculptTest"

const EditorUtils = {
  // 
  // codeBlocksizetextBlock
  toggleStrong: (ev, editor) => {
    ev.preventDefault()
    const [strongMatch] = Editor.nodes(editor, {
      // matchboolundefined
      match: (n) => n.strong && n.size !== "ultra",
      universal: true,
    })
    Transforms.setNodes(
      editor,
      { strong: strongMatch ? null : true },
      { match: (n) => Text.isText(n), split: true }
    )
  },
  toggleOblique: (ev, editor) => {
    ev.preventDefault()
    const [obliqueMatch] = Editor.nodes(editor, {
      match: (n) => n.oblique,
      universal: true,
    })
    Transforms.setNodes(
      editor,
      { oblique: obliqueMatch ? null : true },
      { match: (n) => Text.isText(n), split: true }
    )
  },
  changeLine: (ev, editor, line) => {
    ev.preventDefault()
    const [underlineMatch] = Editor.nodes(editor, {
      match: (n) => n.line === line,
      universal: true,
    })
    Transforms.setNodes(
      editor,
      { line: underlineMatch ? null : line },
      { match: (n) => Text.isText(n), split: true }
    )
  },
  toggleColor: (ev, editor) => {
    ev.preventDefault()
    const [colorMatch] = Editor.nodes(editor, {
      match: (n) => n.color,
      universal: true,
    })
    Transforms.setNodes(
      editor,
      { color: colorMatch ? null : true },
      { match: (n) => Text.isText(n), split: true }
    )
  },
  // unwrapButton! Range.isCollapsed
  toggleCodeSpan: (ev, editor) => {
    ev.preventDefault()

    const types = EditorBasics.getSectoBlockTypes(editor)
    if (
      types.length === 1 &&
      types[0] === "textBlock" &&
      !EditorBasics.isMultipleBlocksSelected(editor)
    ) {
      const [codeSpanMatch] = Editor.nodes(editor, {
        match: (node, path) => {
          if (path.length === 2) {
            if (node.type === "codeSpan") return true
          }
          return false
        },
      })
      // Element.isElement(n) && n.type === "codeSpan"  universalmatchcodeSpan
      // [..XX]XX XXXXmatch[..XX]
      // [..XX]X[XX..][]match
      // universal[..XX]XXmatch[]
      if (codeSpanMatch) {
        Transforms.unwrapNodes(editor, {
          match: (node, path) => {
            if (path.length === 2) {
              if (node.type === "codeSpan") return true
            }
            return false
          },
        })
        // match codeSpancodeSpancodeSpanunwrapwrap
      } else {
        const { selection } = editor
        const isCollapsed = Range.isCollapsed(selection)
        // Range.isCollapsed(selection) Range.isCollapsed is not a function
        // selection
        // isCollapsedfalsetrue

        const first = selection.anchor.path[0]
        const second = selection.anchor.path[1]
        const left = Math.min(selection.anchor.offset, selection.focus.offset)
        const right = Math.max(selection.anchor.offset, selection.focus.offset)
        Transforms.insertText(editor, " ", {
          at: {
            anchor: { path: [first, second], offset: right },
            focus: { path: [first, second], offset: right },
          },
        })
        Transforms.insertText(editor, " ", {
          at: {
            anchor: { path: [first, second], offset: left },
            focus: { path: [first, second], offset: left },
          },
        })
        if (left === right) {
          Transforms.move(editor, {
            distance: 1,
            unit: "offset",
            reverse: true,
            edge: "end", // 
          })
        }
        Transforms.move(editor, {
          distance: 1,
          unit: "offset",
          reverse: true,
          edge: "end", // 
        })

        const codeSpan = {
          type: "codeSpan",
          children: isCollapsed ? [{ text: "code", size: "normal" }] : [],
        }
        // falsetrue
        // style 
        if (isCollapsed) {
          Transforms.insertNodes(editor, codeSpan)
          // SculptTest.insertNodes(editor, codeSpan)
        } else {
          // SculptTest.wrapNodes(editor, codeSpan)
          Transforms.wrapNodes(editor, codeSpan, { split: true })
          Transforms.collapse(editor, { edge: "end" })
        }
        Transforms.delete(editor, {
          at: {
            anchor: {
              path: [first, second],
              offset: editor.children[first].children[second].text.length,
            },
            focus: {
              path: [first, second],
              offset: editor.children[first].children[second].text.length,
            },
          },
          distance: 1,
          reverse: true,
          unit: "character",
        })
        Transforms.delete(editor, {
          at: {
            anchor: { path: [first, second + 2], offset: 0 },
            focus: { path: [first, second + 2], offset: 0 },
          },
          distance: 1,
          unit: "character",
        })
      }
    }
  },
  toggleCodeBlock: (ev, editor) => {
    ev.preventDefault()
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length === 1) {
      if (types[0] === "textBlock") {
        // codeBlock
        // NANAANAAAANBAAA
        // 
        // codeBlock
        const currentBlockMaxIndex = EditorBasics.getPathNthNumMax(
          editor,
          [],
          1
        )
        // const [startPath, endPath] =
        //   EditorBasics.getSectoYotsPathStartAndEnd(editor)
        // if (startPath[0] > 0) {
        //   // block
        //   const previousBlock = Editor.node(editor, [startPath[0] - 1])
        //   if (previousBlock[0].type === "codeBlock") return
        // }
        // if (endPath[0] < currentBlockMaxIndex) {
        //   // block
        //   const nextBlock = Editor.node(editor, [endPath[0] + 1])
        //   console.log(nextBlock)
        //   if (nextBlock[0].type === "codeBlock") return
        // }

        //  

        // textBlock codeSpan 
        //   
        // startendblockblock
        const [start, end] = EditorBasics.getSectoYotsPathNthNumSpan(editor, 1)
        for (let i = start; i <= end; i++) {
          // codeSpan
          const lastPathSecondNum = Editor.end(editor, [i]).path[1]
          for (let j = lastPathSecondNum; j >= 0; j--) {
            const nodeData = Editor.node(editor, [i, j])
            if (nodeData[0]?.type === "codeSpan") {
              Transforms.unwrapNodes(editor, { at: [i, j] })
            }
          }
          // tab
          EditorBasics.replaceTextInTextBlock(editor, i, "\t", "  ")
        }
        // tab
        Transforms.select(editor, {
          anchor: {
            path: [start, 0],
            offset: 0,
          },
          focus: {
            path: [end, 0],
            offset: 0,
          },
        })

        // wrapNodesnodenode
        Transforms.wrapNodes(
          editor,
          { type: "codeBlock" },
          {
            match: (node, path) => {
              return path.length === 1
            },
            // split: true,
            // splitcodeBlock<para><codeblock><para>
            // splitcodeBlock<codeblock>
          }
        )
        // selection
        // match:{}selectionmatch
        const [startPath, endPath] =
          EditorBasics.getSectoYotsPathStartAndEnd(editor)
        for (let i = startPath[1]; i <= endPath[1]; i++) {
          Transforms.setNodes(
            editor,
            { type: "codeLine" },
            { at: [startPath[0], i] }
          )
          Transforms.setNodes(
            editor,
            { size: null },
            { at: [startPath[0], i, 0] }
          )
        }
        // cursorcodeBlock
        Transforms.select(editor, {
          path: [start, 0, 0],
          offset: 0,
        })
      } else if (types[0] === "codeBlock") {
        const howManyCodeLines =
          EditorBasics.getPathNthNumMax(
            editor,
            editor.selection.anchor.path,
            2
          ) + 1
        for (let i = 0; i < howManyCodeLines; i++) {
          Transforms.setNodes(
            editor,
            { size: "normal" },
            { at: [editor.selection.anchor.path[0], i, 0] }
          )
        }
        Transforms.setNodes(
          editor,
          { type: "textBlock" },
          {
            at: [editor.selection.anchor.path[0]],
            match: (node, path) => {
              return path.length === 2
            },
          }
        )
        Transforms.unwrapNodes(editor, {
          match: (n) =>
            !Editor.isEditor(n) &&
            Element.isElement(n) &&
            n.type === "codeBlock",
        })
      }
    }
  },
  changeSize: (ev, editor, size) => {
    ev.preventDefault()
    Transforms.setNodes(
      editor,
      {
        size,
        strong: null,
      },
      { match: (n) => Text.isText(n), split: true }
    )
  },
  enter: (ev, editor) => {
    // codeSpan bug
    const [codeSpanMatch] = Editor.nodes(editor, {
      match: (node, path) => {
        if (path.length === 2) {
          if (node.type === "codeSpan") return true
        }
        return false
      },
    })
    if (codeSpanMatch) {
      ev.preventDefault()
    }
  },
  //  path cursor
  // event
  // codeSpancodeSpan
  // textBlockcodeBlockcodeBlockcodeBlock
  paste: (ev, editor) => {
    // clipboardtextev.preventDefaultpreventTransforms.insertText
    // text
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length !== 1) {
      // block
      ev.preventDefault()
    } else {
      if (types[0] === "codeBlock") {
        // \t"  "\n
        ev.preventDefault()
        navigator.clipboard.readText().then((text) => {
          const texts = text.replaceAll("\t", "  ").split("\n")
          // """"
          const lastText = texts.pop()
          if (lastText !== "") {
            texts.push(lastText)
          }
          for (let i = 0; i <= texts.length - 2; i++) {
            Transforms.insertText(editor, texts[i])
            // codeLine
            const newCodeLineNode = {
              type: "codeLine",
              children: [{ text: "" }],
            }
            Transforms.insertNodes(editor, newCodeLineNode)
          }
          Transforms.insertText(editor, texts[texts.length - 1])
        })
      } else if (types[0] === "textBlock") {
        // /codeSpan
        // codeSpan\t \n
        const [codeSpanMatch] = Editor.nodes(editor, {
          match: (node, path) => {
            if (path.length === 2) {
              if (node.type === "codeSpan") return true
            }
            return false
          },
        })
        if (codeSpanMatch) {
          // codeSpanyots
          // codeSpan
          if (EditorBasics.isSectoYotsPathInitialNumsSame(editor, 2)) {
            // preventDefaulttextinsertText
            ev.preventDefault()
            navigator.clipboard.readText().then((text) => {
              text = text.replaceAll("\t", "  ").replaceAll("\n", "  ")
              Transforms.insertText(editor, text)
            })
          } else ev.preventDefault()
        }
      }
    }
    //
    // ev.preventDefault()
    // // clipboardpreventDefault
    // navigator.clipboard
    //   .readText()
    //   .then((text) => {
    //     console.log(text)
    //     const types = EditorBasics.getSectoBlockTypes(editor)
    //     if (types.length !== 1) {
    //       // block
    //       return
    //     } else {
    //       if (types[0] === "codeBlock" && text.indexOf("\t") !== -1) {
    //         text.replaceAll("\t", "  ")
    //         // codeBlocktabtexttab
    //       } else if (types[0] === "textBlock") {
    //         if (text.indexOf("\t") !== -1 || text.indexOf("\n") !== -1) {
    //           // codeSpantab
    //           const [codeSpanMatch] = Editor.nodes(editor, {
    //             match: (node, path) => {
    //               if (path.length === 2) {
    //                 if (node.type === "codeSpan") return true
    //               }
    //               return false
    //             },
    //           })
    //           if (codeSpanMatch) return
    //         }
    //       }
    //     }
    //     Transforms.insertText(editor, text)
    //   })
    //   .catch((err) => {
    //     console.error("Failed to read clipboard contents: ", err)
    //   })
  },
  // tab
  tab: (ev, editor) => {
    ev.preventDefault()
    // ()tab
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length === 1) {
      const type = types[0]
      if (type === "textBlock") {
        if (EditorBasics.isSectoEmpty(editor)) {
          const [pathLength] =
            EditorBasics.getSectoYotsPathMinAndMaxLength(editor)
          if (pathLength === 2) {
            // codeSpan
            Transforms.insertText(editor, "\t")
          } else {
            // codeSpan
            Transforms.insertText(editor, "  ")
          }
        } else {
          //
        }
      } else if (type === "codeBlock") {
        if (EditorBasics.isSectoEmpty(editor)) {
          Transforms.insertText(editor, "  ")
        } else {
          const [start, end] = EditorBasics.getSectoYotsPathNthNumSpan(
            editor,
            2
          )
          for (let i = start; i <= end; i++) {
            EditorBasics.addTextInCodeLine(
              editor,
              [editor.selection.anchor.path[0], i],
              "  ",
              "start"
            )
          }
        }
      }
    }
    // ctrl+m
    // 
  },
  shiftTab: (ev, editor) => {
    ev.preventDefault()
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length === 1 && types[0] === "codeBlock") {
      const [start, end] = EditorBasics.getSectoYotsPathNthNumSpan(editor, 2)
      for (let i = start; i <= end; i++) {
        EditorBasics.removeTextInCodeLine(
          editor,
          [editor.selection.anchor.path[0], i],
          "  ",
          "start"
        )
      }
    }
  },
  isAbleToInsertLink: (editor) => {
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length === 1 && types[0] === "textBlock") {
      if (
        EditorBasics.isSectoYotsPathNthNumSame(editor, 1) &&
        EditorBasics.isSectoYotsPathNthNumSame(editor, 2)
      ) {
        if (editor.selection.anchor.path.length === 2) {
          return Editor.string(editor, editor.selection)
        }
      }
    }
    return false
  },
  // insert["insert", text]edit["edit", text, url]["no"]
  getLinkActionType: (editor) => {
    const types = EditorBasics.getSectoBlockTypes(editor)
    if (types.length === 1 && types[0] === "textBlock") {
      if (
        EditorBasics.isSectoYotsPathNthNumSame(editor, 1) &&
        EditorBasics.isSectoYotsPathNthNumSame(editor, 2)
      ) {
        if (editor.selection.anchor.path.length === 2) {
          return ["insert", Editor.string(editor, editor.selection)]
        } else if (editor.selection.anchor.path.length === 3) {
          const path = editor.selection.anchor.path
          const nodeData = Editor.node(editor, [path[0], path[1]])[0]
          // eslint-disable-next-line no-prototype-builtins
          if (nodeData.hasOwnProperty("type")) {
            if (nodeData.type === "linkSpan") {
              return ["edit", nodeData.children[0].text, nodeData.url]
            }
          }
        }
      }
    }
    return ["no"]
  },
  insertLink: (editor, text, url) => {
    const { selection } = editor
    const first = selection.anchor.path[0]
    const second = selection.anchor.path[1]
    const left = Math.min(selection.anchor.offset, selection.focus.offset)
    const right = Math.max(selection.anchor.offset, selection.focus.offset)
    Transforms.insertText(editor, " ", {
      at: {
        anchor: { path: [first, second], offset: right },
        focus: { path: [first, second], offset: right },
      },
    })
    Transforms.insertText(editor, " ", {
      at: {
        anchor: { path: [first, second], offset: left },
        focus: { path: [first, second], offset: left },
      },
    })
    if (left === right) {
      Transforms.move(editor, {
        distance: 1,
        unit: "offset",
        reverse: true,
        edge: "end", // 
      })
    }
    Transforms.move(editor, {
      distance: 1,
      unit: "offset",
      reverse: true,
      edge: "end", // 
    })

    const linkSpan = {
      type: "linkSpan",
      url,
      children: [{ text, size: "normal" }],
    }
    // falsetrue
    // style 
    if (Range.isCollapsed) {
      Transforms.insertNodes(editor, linkSpan)
    } else {
      Transforms.wrapNodes(editor, linkSpan, { split: true })
      Transforms.collapse(editor, { edge: "end" })
    }
    Transforms.delete(editor, {
      at: {
        anchor: {
          path: [first, second],
          offset: editor.children[first].children[second].text.length,
        },
        focus: {
          path: [first, second],
          offset: editor.children[first].children[second].text.length,
        },
      },
      distance: 1,
      reverse: true,
      unit: "character",
    })
    Transforms.delete(editor, {
      at: {
        anchor: { path: [first, second + 2], offset: 0 },
        focus: { path: [first, second + 2], offset: 0 },
      },
      distance: 1,
      unit: "character",
    })
  },
  editLink: (editor, text, url) => {
    const path = editor.selection.anchor.path
    // urlsetNodestext
    Transforms.setNodes(
      editor,
      { url },
      {
        at: [path[0], path[1]],
      }
    )
    Transforms.insertText(editor, text, {
      at: path,
    })
  },
  test: (ev, editor) => {
    ev.preventDefault()
  },
}

export default EditorUtils
